<script>
import BaseModal from '@/components/BaseModal.vue';
import ProductCounter from '@/components/ProductCounter.vue';
import ProductColor from '@/components/ProductColor.vue';
import { defineComponent } from 'vue';
import useProduct from '@/hooks/useProduct';

export default defineComponent({
  props: {
    productId: { type: [Number, String], required: true }
  },
  components: {
    ProductCounter, ProductColor, BaseModal
  },
  setup(props) {
    // const $route = useRoute();
    const { product, fetchProduct, status: productStatus, doAddToCart, productAmount, productAdded, productAddSending, isShowAddedMassage } = useProduct()
    fetchProduct(props.productId)

    return {
      productAmount,
      productData: product,
      productStatus,
      productAdded,
      productAddSending,
      isShowAddedMassage,
      product,

      doAddToCart,
    }
  },
})

</script>

